    // fdm
export class fdmWYY {
    private StringUtil:any;
    public timeToDateYMDHMS(time:number,yh:number = 0,splitStr1:string = '.',splitStr2:string = ':'){
        let date = new Date(time*1000);
        let year = date.getFullYear();
        let month = this.StringUtil.supplement(date.getMonth() + 1, 2);
        let day = this.StringUtil.supplement(date.getDate(), 2);
        let hours = this.StringUtil.supplement(date.getHours(), 2);
        let minutes = this.StringUtil.supplement(date.getMinutes(), 2);
        let seconds = this.StringUtil.supplement(date.getSeconds(), 2);
        let m1 = year + splitStr1 + month;
        let m2 = year + splitStr1 + month + splitStr1 + day;
        let m3 = month + splitStr1 + day;
        let n1 = hours + splitStr2 + minutes;
        let n2 = hours + splitStr2 + minutes + splitStr2 + seconds;
        let strsArr = [m2 + ' ' + n2,m2 + ' ' + n1, m2,m1, m3, n2, n1];
        return strsArr[yh];
    }
}

export default class DataManager {

    private static instance = null;
    public static Instance(): DataManager {
        if (DataManager.instance == null) {
            DataManager.instance = new DataManager();
        }
        return DataManager.instance;
    }

    @save public signdays: number = 0;//签到次数
    @save public signtime: number = 0;//最近一次签到时间
    @save public share_count: number = 0;
    public share_count_total: number = 0;
    public watch_ad_count: number = 0;
    public watch_ad_count_total: number = 0;


    @save public savedatatime: number = 0;
    @save public signinfo: SignInfo = { sign_index: 0, sign_time: 0, sign_beisu: 0 };


    public getAllSData(): object {
        var data = {}
        for (var i = 0; i < savepars.length; ++i) {
            data[savepars[i]] = this[savepars[i]];
        }
        return data;
    }
}

